import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
// import Input from "../../../../components/uielements/input";
import Button from "../../../../components/uielements/button";
import IntlMessages from "../../../../components/utility/intlMessages";
import notification from "../../../../components/notification";
// import SignInStyleWrapper from "./signin.style";

// Action
import authAction from "../../../../redux/auth/actions";
import serverAction from "../../../../redux/serverSetting/actions";

// Form
import Form from "../../../../components/uielements/form";
import {
  APP_VERSION,
  baseUrl,
  currLanguage,
  isElectron
} from "../../../../helpers/helper";
import { Row, Col, Modal, Icon, Input } from "antd";

// /********************** new style */
// import styled from "styled-components";
import LogoContainer from "../../../../image/logo-bg.png";
import { siteConfig } from "../../../../settings";
import SignInStyleNewWrapper from "./signin.style";

const { checkLogin, login, clearWarning, clearError } = authAction;
const { clearSuccess } = serverAction;
const confirm = Modal.confirm;

// Setting Form
const FormItem = Form.Item;
const formItemLayout = {
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 24 }
  }
};

class SignIn extends Component {
  state = {
    redirectToReferrer: false,
    user: {
      username: null,
      password: null
    }
  };

  componentDidMount = () => {
    document.title = siteConfig.documentTitle;

    if (isElectron()) {
      window.ipcRenderer.on("APP_READY", this.readyListener);
      window.ipcRenderer.on("APP_BEFORE_QUIT", this.beforeQuitListener);
    }

    const new_server = localStorage.getItem("new-server");
    if (new_server) {
      localStorage.clear();
      localStorage.setItem("server", new_server);
    }

    const server = localStorage.getItem("server");
    if (server !== null && this.props.serverSetting.success) {
      notification("success", this.props.serverSetting.success);
      clearSuccess();
    }

    if (this.props.auth.bearer && !this.props.auth.loading) {
      return <Redirect to="/dashboard" />;
    }
  };

  componentDidUpdate = prevProps => {
    const { warning, error } = this.props.auth;
    const { clearError } = this.props;

    if (prevProps.auth.warning !== warning && warning) {
      this.showNotification();
    }

    if (prevProps.auth.error !== error && error) {
      notification("error", error);
      clearError();
    }
  };

  // Show notifikasi login
  showNotification = () => {
    const { warning } = this.props.auth;

    confirm({
      title: "Warning",
      content: warning,
      icon: <Icon type="info-circle" />,
      okText: "YES",
      cancelText: "NO",
      onOk: () => {
        const { clearWarning } = this.props;
        this.handleLogin();
        clearWarning();
      },
      onCancel: () => {
        const { clearWarning } = this.props;
        clearWarning();
      }
    });
  };

  readyListener = (event, args) => console.log(event, args);

  beforeQuitListener = () => {
    // console.log("quit listener signin");
    window.ipcRenderer.send("APP_CAN_QUIT", "from signin");
  };

  componentWillUnmount = () => {
    if (isElectron()) {
      window.ipcRenderer.removeAllListeners();
    }
  };

  handleChange = event => {
    const value = event.target.value;
    const name = event.target.name;
    this.setState({
      user: {
        ...this.state.user,
        [name]: value
      }
    });
  };

  handleButton = () => {
    const { user } = this.state;
    if (user.username && user.password) {
      return false;
    } else {
      return true;
    }
  };

  handleLogin = () => {
    const { login } = this.props;
    login(this.state.user);
    // this.props.history.push("/dashboard");
  };

  handleCheckLogin = () => {
    const { checkLogin } = this.props;
    checkLogin(this.state.user);
  };

  handleEnter = () => {
    const { user } = this.state;
    if (user.username && user.password) {
      this.handleCheckLogin();
    }
  };

  form = () => {
    return (
      <div className="isoSignInForm">
        <FormItem
          {...formItemLayout}
          hasFeedback
          validateStatus={this.props.auth.status}
          style={{ marginBottom: "12px" }}
        >
          <Input
            name="username"
            // size="large"
            placeholder="Username"
            onChange={this.handleChange}
            onPressEnter={this.handleEnter}
          />
        </FormItem>

        <FormItem
          {...formItemLayout}
          hasFeedback
          validateStatus={this.props.auth.status}
          style={{ marginBottom: "12px" }}
        >
          <Input
            name="password"
            // size="large"
            type="password"
            placeholder="Password"
            onChange={this.handleChange}
            onPressEnter={this.handleEnter}
          />
        </FormItem>

        {this.submitButton()}

        <div className="isoCenterComponent isoHelperWrapper">
          <Link to="" className="isoForgotPass">
            <IntlMessages id="page.signInForgotPass" />
          </Link>
        </div>
      </div>
    );
  };

  submitButton = () => {
    return (
      <>
        <Row style={{ marginBottom: "12px" }}>
          <Col span={24}>
            <Button
              type="primary"
              onClick={this.handleCheckLogin}
              loading={this.props.auth.loading}
              style={{ width: "100%" }}
              htmlType="button"
              disabled={this.handleButton()}
            >
              <IntlMessages id="page.signInButton" />
            </Button>
          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <Link to={`/server-setup`}>
              <Button type="default" style={{ width: "100%" }}>
                <IntlMessages id="changeUrl" />
              </Button>
            </Link>
          </Col>
        </Row>
      </>
    );
  };

  // new style
  submitButtonNew = () => {
    return (
      <>
        <div style={{ marginBottom: "12px", marginTop: "15px" }}>
          <Button
            type="primary"
            onClick={this.handleCheckLogin}
            loading={this.props.auth.loading}
            htmlType="button"
            disabled={this.handleButton()}
          >
            <IntlMessages id="page.signInButton" />
          </Button>
        </div>
      </>
    );
  };
  // new style

  render() {
    // console.log(this.props);
    const serverAddress = baseUrl();

    if (this.props.auth.bearer && !this.props.auth.loading) {
      return <Redirect to="/dashboard" />;
    }

    if (!serverAddress) {
      return <Redirect to="/server-setup" />;
    }

    return (
      <>
        {/* <SignInStyleWrapper className="isoSignInPage">
          <div className="isoLoginContentWrapper">
            <div className="isoLoginContent">
              <div className="isoLogoWrapper">
                <Link to="/dashboard">
                  <IntlMessages id="page.signInTitle" />
                </Link>
              </div>
              {this.form()}
            </div>
          </div>
        </SignInStyleWrapper> */}
        <SignInStyleNewWrapper>
          <div className="middle-content">
            <div className="middle-content-child sign-in-form">
              <h2>
                <IntlMessages id="page.signInButton" />
              </h2>
              <div className="isoSignInForm">
                <FormItem
                  {...formItemLayout}
                  hasFeedback
                  validateStatus={this.props.auth.status}
                  style={{ marginBottom: "12px" }}
                  label={<IntlMessages id="users.add.username" />}
                >
                  <Input
                    name="username"
                    // size="large"
                    onChange={this.handleChange}
                    onPressEnter={this.handleEnter}
                  />
                </FormItem>

                <FormItem
                  {...formItemLayout}
                  hasFeedback
                  validateStatus={this.props.auth.status}
                  style={{ marginBottom: "12px" }}
                  label={<IntlMessages id="users.add.password" />}
                >
                  <Input.Password
                    name="password"
                    // size="large"
                    type="password"
                    onChange={this.handleChange}
                    onPressEnter={this.handleEnter}
                  />
                </FormItem>

                {this.submitButtonNew()}

                {/* <div className="isoHelperWrapper">
          <Link to="" className="isoForgotPass">
            <IntlMessages id="page.signInForgotPass" />
          </Link>
        </div> */}
              </div>
            </div>
            <div className="middle-content-child to-server-setup">
              <div className="logo-container">
                <figure>
                  <img
                    src={LogoContainer}
                    // style={{
                    //   backgroundImage: `"url(" + ${LogoSample}+")"`
                    // }}
                  ></img>
                </figure>
                <div>
                  <span>
                    <IntlMessages id="appVersion" />
                  </span>
                  <h4>{APP_VERSION}</h4>

                  <span>
                    <IntlMessages id="server" />
                  </span>
                  {serverAddress ? (
                    <h4>{serverAddress}</h4>
                  ) : (
                    <div>
                      <IntlMessages id="server.none" />
                    </div>
                  )}

                  <div style={{ marginTop: "10px" }}>
                    <Link to={`/server-setup`}>
                      <Button type="default">
                        <IntlMessages id="changeUrl" />
                      </Button>
                    </Link>
                  </div>
                  <div>
                    <div className="manual-book">
                      <a
                        target="_blank"
                        rel="noopener noreferrer"
                        href={
                          serverAddress +
                          // (currLanguage()
                          //   ? currLanguage().toLowerCase()
                          //   : "en") +
                          // "/documentation"
                          "documentation"
                        }
                        className="manual-book-link"
                      >
                        <IntlMessages id="manualBook" />
                      </a>
                    </div>
                  </div>
                  {/* <div style={{ marginTop: "10px" }}>
                    <Link to="">
                      <Button type="default">
                        <IntlMessages id="manualBook" />
                      </Button>
                    </Link>
                  </div> */}
                </div>
              </div>
            </div>
          </div>
        </SignInStyleNewWrapper>
      </>
    );
  }
}

export default connect(
  state => ({
    auth: state.auth,
    serverSetting: state.serverSetting
  }),
  { checkLogin, login, clearWarning, clearError, clearSuccess }
)(SignIn);
