// new style
const SignInStyleNewWrapper = styled.div`
  align-items: center;
  background-image: linear-gradient(to right, #fff, #ededed);
  display: flex;
  justify-content: center;
  height: 100vh;

  .middle-content {
    align-items: center;
    background: transparent;
    border-radius: 20px;
    box-shadow: 0px 0px 6px 1px #9696962b;
    display: flex;
    height: 75vh;
    width: 55%;

    .middle-content-child {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }

    .middle-content-child:first-child {
      flex: 2;

      &.sign-in-form {
        background-color: #fff;
        border-bottom-left-radius: 20px;
        border-top-left-radius: 20px;
        padding: 3rem 4rem;

        h2 {
          font-size: 4em;
          font-weight: bolder;
          margin-bottom: 0;
        }

        .ant-form-item-label label:after {
          content: '';
        }

      }

    }

    .middle-content-child:last-child {
      flex: 1;

      &.to-server-setup {
        background-color: #2f5bb2;
        border-bottom-right-radius: 20px;
        border-top-right-radius: 20px;
        color: #fff;
        padding: 3rem;

        .logo-container {
          figure > img {
            background-image: url('${Logo}');
            background-position-x: left;
            background-position-y: center;
            background-repeat: no-repeat;
            background-size: contain;
            max-width: 200px;
          }
          margin-bottom: 1em;
        }

        h4 {
          color: #fff;
          font-size: 1rem;
          margin-bottom: 1.5em;
          position: relative;

          &:after {
            content: '';
            background: #9fbf40;
            height: 2px;
            left: 0;
            position: absolute;
            top: 100%;
            width: 100%;
          }
        }

      }

    }
  }

  .manual-book {
    margin-bottom: 20px;
    margin-top: 20px;

    a.manual-book-link {
      color: #fff;
      font-size: medium;
      font-weight: 500;
      &:hover {
        text-decoration: underline;
      }
    }
  }

  @media (max-width: 1024px){
    .middle-content {
      width: 75%;
    }
  }

  @media (max-width: 768px){
    height: auto;
    min-height: 100vh;
    padding: 2rem;

    .middle-content {
      flex-direction: column;
      height: auto;
      min-height: 75vh;
      width: 85%;

      .middle-content-child {
        width: 100%;
      }
      
      .middle-content-child:first-child {
        &.sign-in-form {
          background-color: #fff;
          border-bottom-left-radius: 0px;
          border-top-right-radius: 20px;
        }
      }

      .middle-content-child:last-child {
        &.to-server-setup {
          border-bottom-left-radius: 20px;
          border-top-right-radius: 0px;
        }
      }

    }
    
  }

  @media (max-width: 480px){
    .middle-content {
      flex-direction: column;
      width: 90%;
    }
  }
`;
// /new style

export default WithDirection(SignInStyleNewWrapper);
